import React, { useState, useCallback } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import IconButton from '@material-ui/core/IconButton';
import ClearIcon from '@material-ui/icons/Clear';
import InputAdornment from '@material-ui/core/InputAdornment';
import TextField from '@material-ui/core/TextField';

import FileBrowser from '../containers/FileBrowser';
import useTranslation from '../hooks/useTranslation';
import { FILE_TYPES } from '../modules/rockhopper/constants';

const useStyles = makeStyles((theme) => ({
  fileButton: {
    width: "100%",
    justifyContent: "left"
  },
  homeRow: {
    color: theme.palette.primary.main
  },
  fileList: {
    position: "relative",
    margin: theme.spacing(2),
    width: "calc(100% - " + theme.spacing(4) + ")",
    overflow: "scroll"
  },
  fileItem: {
    position: "absolute"
  },
  fileContainer: {
    margin: theme.spacing(2),
    width: "calc(100% - 32px)"
  },
  fileLabel: {
    fontWeight: "bold",
  },
  fileName: {
    whiteSpace: "nowrap",
    overflow: "hidden",
    textOverflow: "ellipsis"
  },
  filter: {
    width: "100%"
  },
  fileDialog: {
    height: "min(600px, 100%)",
    width: "min(500px, 100%)"
  },
  closeButton: {
    position: "absolute",
    right: theme.spacing(1),
    top: theme.spacing(1)
  },
  percentage: {
    marginLeft: theme.spacing(1)
  }
}));

const FileBrowserDialog = (props) => {
  const {
    open,
    onClose,
    rootPath,
    filesMap,
    fileType,
    onFileOpen,
    // onFileDelete,
    onFileCopy,
    onHome,
    queuedFiles = [],
  } = props;
  const { t } = useTranslation();

  const [ selected, setSelected ] = useState({});
  const [ fileFilter, setFileFilter ] = useState("");
  const queuedFileList = Object.values(queuedFiles).map((file, index) => ({ ...file, index }));
  queuedFileList.sort((a,b) => { return b.progress-a.progress });

  const handleClose = () => {
    if(onClose){
      onClose();
    }
  };
  const handleFileOpen = (path) => {
    if(onFileOpen) {
      onFileOpen(path, fileType);
    }
  };
  const handleSelect = useCallback((e) => {
    if(e.target.checked) {
      setSelected({ ...selected, [e.target.value]: true });
    } else {
      setSelected({ ...selected, [e.target.value]: false });
    }
  }, [ selected, setSelected ]);
  const handleCopySelected = useCallback((e) => {
    if(onFileCopy) {
      const copyFiles = Object.keys(selected).filter((path) => selected[path]);
      copyFiles.forEach((path) => {
        onFileCopy("/media/usb0/" + path, fileType);
      });
      setSelected({});
    }
  }, [ fileType, onFileCopy, selected, setSelected ]);

  const handleClearFilter = useCallback((e) => {
    setFileFilter("");
  }, [ setFileFilter ]);
  const handleFilterChange = useCallback((e) => {
    setFileFilter(e.target.value);
  }, [ setFileFilter ]);
  const classes = useStyles();

return <>
    <Dialog open={open} onClose={handleClose} classes={ { paper: classes.fileDialog }}>
    <DialogTitle>
    <span>{ t("openGCode") }</span>
    <IconButton className={classes.closeButton} onClick={handleClose} size="small"><ClearIcon/></IconButton>
    </DialogTitle>
    <DialogContent>
    <TextField 
      placeholder={t("filter")} 
      value={fileFilter} 
      className={classes.filter} 
      onChange={handleFilterChange} 
      InputProps={{ endAdornment: (fileFilter.length > 0 ? <InputAdornment position="end"><IconButton size="small" onClick={handleClearFilter}><ClearIcon/></IconButton></InputAdornment> : null) }}
    />
    <FileBrowser
            //Get the name for the root USB folder from the mountPath property
            fileFilter={fileFilter}
            filesMap={filesMap}
            fileType={FILE_TYPES.USB_PROGRAM}
            rootPath={rootPath}
            onFileOpen={(path, fileType) => (handleFileOpen(path, fileType))}
            // onFileDelete={handleDeleteSelected}
            onHome={onHome}
            onSelectFile={handleSelect}
            selectedFiles={selected}
            // fileFilter={filter}
          />
    </DialogContent>
    <DialogActions>
      <Button color="primary" onClick={handleCopySelected} disabled={Object.keys(selected).filter((path) => selected[path]).length === 0}>{t("copyToDisk")}</Button>
      {/* <Button color="secondary" onClick={handleDeleteSelected} disabled={Object.keys(selected).filter((path) => selected[path]).length === 0}>{t("deleteSelected")}</Button> */}
      <Button onClick={handleClose} >{ t("cancel") }</Button>
    </DialogActions>
  </Dialog>
  </>;
};
export default FileBrowserDialog;
