import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Box from '@material-ui/core/Box';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import Checkbox from '@material-ui/core/Checkbox';
import ArrowUpIcon from '@material-ui/icons/KeyboardArrowUp';
import FolderIcon from '@material-ui/icons/Folder';
import FolderOpenIcon from '@material-ui/icons/FolderOpen';
import HomeIcon from '@material-ui/icons/Home';
import KeyboardArrowRightIcon from '@material-ui/icons/KeyboardArrowRight';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import useTranslation from '../hooks/useTranslation';

const selectFromMap = (map, arr) => {
  var result = arr.reduce(function(o, name) { return o && o[name]; }, map);
  return result
}

const useStyles = makeStyles((theme) => ({
  fileButton: {
    width: "100%",
    justifyContent: "left"
  },
  homeRow: {
    color: theme.palette.primary.main
  }
}));

const FileBrowser = (props) => {
  const {
    rootPath,
    filesMap,
    fileType,
    fileFilter,
    selectedFiles,
    onFileOpen,
    onHome,
    onSelectFile,
  } = props;
  const { t } = useTranslation();

  const [ path, setPath ] = useState([]);

  const handleGoUp = () => {
    path.length > 0 ? setPath(path.slice(0,-1)) : onHome();
  }
  const handleFileOpen = (path) => {
    if(onFileOpen) {
      onFileOpen(path,fileType);
    }
  };
  const handleSelectFile = (val) => {
    if(onSelectFile){
      onSelectFile(val)
    }
  }

  const classes = useStyles();
  const currentLevel = selectFromMap(filesMap, path);
  const currentLevelDirs = currentLevel ? Object.keys(currentLevel).filter((key) => currentLevel[key] !== null) : null;
  const currentLevelFiles = currentLevel ? Object.keys(currentLevel).filter((key) => currentLevel[key] === null) : [];
  const filteredFileList = currentLevelFiles.filter(( file ) => file.toLowerCase().includes(fileFilter.toLowerCase()));

  return <>
    <Box>
    <List>
      <ListItem className={classes.homeRow}>
        <IconButton
          onClick={() => {setPath([]); onHome();}}
          color="primary"
        >
          <HomeIcon/>
        </IconButton>
        <IconButton
          onClick={handleGoUp}
          color="primary"
        >
          <ArrowUpIcon/>
        </IconButton>
        <FolderOpenIcon/>{[t("USB")].concat(path).join("/")}
      </ListItem>
      {
        currentLevelDirs && currentLevelDirs.map((dir) => {
          return <ListItem key={dir}>
              <Button
                onClick={() => {setPath(path.concat(dir))}}
              >
                <KeyboardArrowRightIcon/>
                <FolderIcon/>
                {dir}
              </Button>
          </ListItem>
        })
      }
      {
        filteredFileList && filteredFileList.map((file) => {
          return <ListItem key={file}>
              <Checkbox value={path + file} checked={selectedFiles[path + file] ? true : false} onClick={handleSelectFile} />
              <Button
                classes={{ root: classes.fileButton }}
                onClick={() => {handleFileOpen([rootPath].concat(path).concat(file).join("/"))}}
              >
                {file}
              </Button>
          </ListItem>
        })
      }
    </List>
    </Box>
  </>;
};
export default FileBrowser;
