import { connect } from 'react-redux';
import FileBrowserDialog from '../components/FileBrowserDialog';
import actions from '../modules/rockhopper/actions';

export default connect( 
(state, ownProps) => ({
  
}), 
(dispatch, ownProps) => ({ 
  onFileCopy: (path, fileType) => {
    //Currently only used to copy from USB to ncfiles
    dispatch(actions.put.fileCopy({ data: [ path, fileType, "~/ncfiles/"  ] }))
  }
}))(FileBrowserDialog); 
